.book-color {
  color: #8eadB1;
}

@media only screen and (max-width: 767px) {
  #book-panel:before {
    content: '';
    background-color: #2c3338;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: -100;
    display: block;
  }
  #book-panel:after {
    content: '';
    display: none;
  }
  #bookImage {
    position: fixed;
    width: 100%;
    height: auto;
    left: 0;
    right: 0;
    top: 5rem;
    bottom: 0;
    z-index: -100;
    display: block;
  }
  #book-panel {
    margin: 5rem 0;
  }
  .book-wrapper {
    width: 90%;
    height: auto;
    background-color: rgba(10, 10, 10, 0.7);
    margin: 8rem auto 1rem auto;
    padding: 2.5rem;
    border-radius: 4px;
    box-shadow: 0 0 10px black;
  }
  .book-wrapper > .book-heading {
    font-family: "Sansita", sans-serif;
    font-size: 4.2rem;
    color: #8eadB1;
    text-align: center;
    padding-top: 1rem;
    margin-bottom: 5rem;
  }
  .book-wrapper > .book-subheading {
    font-family: "Roboto Slab", serif;
    font-size: 2rem;
    width: 90%;
    color: #e4cd8a;
    text-align: center;
  }
  .book-all-wrapper {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  .book-one-wrapper {
    margin: 4rem 0 2rem 0;
    min-width: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .book-image {
    margin: 1rem 1.5rem;
    background: #2c3338;
    padding: 1rem;
    border-radius: 5px;
    border: 2px solid #e4cd8a;
    transition: all 0.3s linear;
  }
  .book-image > img {
    display: block;
    margin-left: 0;
    margin-right: 0;
    width: auto;
    height: 33%;
    flex-basis: 100%;
    z-index: 1;
  }
  .book-image:hover {
    transition: all 0.3s linear;
    transform: scale(1.6);
    border: 3px solid #2c3338;
    background: #e4cd8a;
    z-index: 1;
  }
  a.book-title, p.book-title {
    font-family: "Playfair Display", serif;
    font-size: 2.4rem;
    padding-top: 3.5rem;
    width: 95%;
    color: #e4cd8a;
    text-align: center;
    line-height: 2.8rem;
    margin: auto;
    transition: all 0.3s linear;
  }
  a.book-title:hover {
    color: #8eadB1;
  }
  .book-show-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    margin: 7rem 0;
  }
  .book-description {
    width: 100%;
    padding-top: 4rem;
    font-family: "Roboto Slab", serif;
    font-size: 2rem;
    font-weight: 600;
    color: #e4ddce;
  }
  .book-details {
    display: flex;
    flex-wrap: wrap;
    align-content: flex-start;
  }
  .book-details > .book-title {
    width: 100%;
  }
  .book-button {
    display: block;
    font-family: "Sansita", sans-serif;
    font-size: 2.6rem;
    font-weight: 600;
    width: 100%;
    margin: 2rem 0;
    padding: 1.5rem;
    background-color: #638a8f;
    text-align: center;
    color: #e4cd8a;
    border: none;
    border-radius: 4px;
    transition: all 0.3s linear;
  }
  .book-button:hover {
    color: #e4cd8a;
    background-color: #445f62;
  }
}

@media only screen and (orientation: portrait) and (max-width: 767px) {
  #bookImage {
    width: auto;
    height: calc(100% - 10rem);
  }
  #book-panel:after {
    content: '';
    background-color: #2c3338;
    position: fixed;
    bottom: 0;
    left: 0;
    height: 5rem;
    width: 100%;
    z-index: 100;
    display: block;
  }
  .book-wrapper {
    width: 95%;
    margin: 7rem auto 3rem auto;
  }
  .book-image > img {
    height: 40%;
  }
  .book-image:hover {
    transform: scale(1.4);
  }
  a.book-title {
    font-family: "Playfair Display", serif;
    font-size: 3rem;
    padding-top: 4rem;
    width: 95%;
    line-height: 3.4rem;
    margin: auto;
    transition: all 0.3s linear;
  }
}

