@media only screen and (max-width: 767px) {
  #project-panel:before {
    content: '';
    background-color: #2c3338;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: -100;
    display: block;
  }
  #project-panel:after {
    content: '';
    display: none;
  }
  #projectImage {
    position: fixed;
    width: 100%;
    height: auto;
    left: 0;
    right: 0;
    top: 5rem;
    bottom: 0;
    z-index: -100;
    display: block;
  }
  .project-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    align-items: center;
    width: 90%;
    min-height: 100vh;
    background-color: rgba(10, 10, 10, 0.7);
    margin: 8rem auto 1rem auto;
    padding: 2rem;
    border-radius: 4px;
    box-shadow: 0 0 10px black;
  }
  .project-wrapper > .project-heading {
    font-family: "Sansita", sans-serif;
    font-size: 4.2rem;
    color: #8eadB1;
    text-align: center;
    padding-top: 1rem;
  }
  .project-wrapper > .project-subheading {
    font-family: "Roboto Slab", serif;
    font-size: 2rem;
    width: 90%;
    color: #e4cd8a;
    text-align: center;
    margin-bottom: 5rem;
  }
  #projectImage {
    position: fixed;
    width: 100%;
    height: auto;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: -100;
    display: block;
  }
  .project-title {
    font-family: "Playfair Display", serif;
    font-size: 3rem;
    padding-top: 1rem;
    width: 100%;
    color: #e4cd8a;
    text-align: center;
    line-height: 3.2rem;
    margin: auto;
    transition: all 0.3s linear;
  }
  .project-cointainer-example {
    display: flex;
    flex-direction: column;
    width: 100%;
    margin-top: 5rem;
  }
  .project-cointainer-example .project-paragraph {
    min-width: 100%;
    width: 100%;
    font-family: "Roboto Slab", serif;
    font-size: 2rem;
    text-align: center;
    line-height: 2.4rem;
    color: #e4ddce;
    margin: 1rem 0 0 0;
  }
  .project-cointainer-example .project-container-image {
    min-width: 100%;
    width: 100%;
    margin: 5rem 0 0 0;
  }
  .project-cointainer-example .project-container-image > .project-image {
    width: 100%;
    transition: all 0.3s linear;
    border: 3px #e4cd8a solid;
    border-radius: 4px;
  }
  .project-wrapper-technology {
    width: 90%;
    display: flex;
    flex-wrap: wrap;
    margin: 5rem 0;
    justify-content: space-around;
  }
  .project-container-technology {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .project-container-technology > .project-image-technology {
    width: auto;
    height: 8rem;
    margin: 1rem;
  }
  .project-container-technology > .project-name-technology {
    font-family: "Sansita", sans-serif;
    font-size: 2.4rem;
    color: #e4cd8a;
    margin: 1rem;
  }
  .project-button {
    display: inline-block;
    font-family: "Sansita", sans-serif;
    font-size: 2rem;
    font-weight: 600;
    width: 90%;
    margin: 5rem 5% 3rem;
    padding: 1.5rem;
    text-align: center;
    background-color: #638a8f;
    color: #e4cd8a;
    border: none;
    border-radius: 4px;
    transition: all 0.3s linear;
  }
  .project-button:hover {
    background-color: #445f62;
    color: #e4cd8a;
  }
}

@media only screen and (orientation: portrait) and (max-width: 767px) {
