@media only screen and (max-width: 767px) {
  #canvas-panel:before {
    content: '';
    background-color: #2c3338;
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: -100;
    display: block;
  }
  #canvas-panel:after {
    content: '';
    display: none;
  }
  #canvas-panel {
    margin: 5rem 0;
  }
  #canvasImage {
    position: fixed;
    width: 100%;
    height: auto;
    left: 0;
    right: 0;
    top: 5rem;
    bottom: 0;
    z-index: -100;
    display: block;
  }
  .canvas-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    align-items: center;
    width: 90%;
    height: 100%;
    background-color: rgba(10, 10, 10, 0.7);
    margin: 8rem auto 1rem auto;
    border-radius: 4px;
    box-shadow: 0 0 10px black;
  }
  .canvas-wrapper > .canvas-heading {
    font-family: "Sansita", sans-serif;
    font-size: 4.2rem;
    color: #8eadB1;
    text-align: center;
    padding-top: 2rem;
  }
  .canvas-wrapper > .canvas-subheading {
    font-family: "Roboto Slab", serif;
    font-size: 2rem;
    width: 90%;
    color: #e4cd8a;
    text-align: center;
    margin-bottom: 3rem;
  }
  .canvas-all-wrapper {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
  }
  .canvas-one-wrapper {
    margin: 4rem 2rem 4rem 2rem;
    max-width: 100%;
    min-width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-content: center;
    align-items: center;
  }
  .canvas-image {
    width: 50%;
    margin: 1rem 1.5rem;
    background: #2c3338;
    padding: 1rem;
    border-radius: 5px;
    border: 2px solid #e4cd8a;
    transition: all 0.3s linear;
  }
  .canvas-title {
    font-family: "Playfair Display", serif;
    font-size: 4rem;
    padding: 2rem 0;
    width: 90%;
    color: #e4cd8a;
    text-align: center;
    line-height: 4.6rem;
    margin: auto;
    transition: all 0.3s linear;
  }
  .canvas-description {
    width: 90%;
    padding-top: 3rem;
    font-family: "Roboto Slab", serif;
    font-size: 2rem;
    font-weight: 600;
    color: #e4ddce;
    text-align: center;
  }
  .canvas-link {
    font-family: "Sansita", sans-serif;
    font-size: 2rem;
    font-weight: 600;
    width: 80%;
    color: #e4cd8a;
    line-height: 2rem;
    padding: 1.5rem 3rem;
    margin: auto;
    border-radius: 4px;
    margin: 2rem 0;
    background-color: #638a8f;
    transition: all 0.3s linear;
    text-align: center;
  }
  .canvas-link:hover {
    color: #e4cd8a;
    background-color: #445f62;
  }
  .canvas-buttons {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    margin: 4rem 2rem;
  }
  .canvas-wrapper-technology {
    width: 90%;
    display: flex;
    flex-wrap: wrap;
    margin: 3rem auto;
    justify-content: space-around;
  }
  .canvas-container-technology {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .canvas-container-technology > .canvas-image-technology {
    width: auto;
    height: 8rem;
    margin: 1rem;
  }
  .canvas-container-technology > .canvas-name-technology {
    font-family: "Sansita", sans-serif;
    font-size: 2.4rem;
    color: #e4cd8a;
  }
}

@media only screen and (orientation: portrait) and (max-width: 767px) {
  #canvas-panel:after {
    content: '';
    background-color: #2c3338;
    position: fixed;
    bottom: 0;
    left: 0;
    height: 5rem;
    width: 100%;
    z-index: 100;
    display: block;
  }
  #canvasImage {
    width: auto;
    height: calc(100% - 10rem);
  }
}

